.header {
  border-bottom: 1px solid #e5e5e5;
  max-width: 1920px;
  width: 100%;

  &-container {
    max-width: 1520px;
    padding: 30px 40px 25px 40px;
    display: flex;
    margin: 0 auto;
  }

  &__logo {
    justify-self: center;
    margin: auto;
  }
}

@media (max-width: 635px) {
  .header-container {
    padding: 10px 15px;
  }
}